import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import PostList from "../components/post-list";
import styled from "styled-components";
import StyledLink from "../components/styled-link";
// import { openPopupWidget } from "react-calendly";

const HomePage = ({ data }) => {
  // const posts = data.allMarkdownRemark.nodes;
  const intro = data.markdownRemark.html;
  const url = data.markdownRemark.frontmatter.url;
  const cta_mail = data.markdownRemark.frontmatter.cta_mail;
  const cta_call = data.markdownRemark.frontmatter.cta_call;
  // const title = data.markdownRemark.frontmatter.title;
  console.log(data.markdownRemark);
  return (
    <Layout>
      <MainContainer>
        <FlexCont>
          <VideoContainer>
            <VideoElement
              id="ytplayer"
              type="text/html"
              allowFullScreen="true"
              src={url}
              csp="frame-src youtube.com www.youtube.com;"
              // frameborder="0"
            />
            <TabletShape />
          </VideoContainer>
        </FlexCont>
        <Intro>
          <div
            dangerouslySetInnerHTML={{
              __html: intro,
            }}
          />

          <div class="button-container">
            {/* <a href=''><img alt='Get it on Google Play' src='/media/google.png'/></a>
<a href=""><img src="/media/apple.png" alt="Download on the App Store"></a> */}
            <a href="mailto:info@improove.fit" class="btn btn-3">
              {cta_mail}
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://calendly.com/improove-trainer/15min"
              // onClick={() =>
              //   openPopupWidget({
              //     url:
              //       "https://calendly.com/improove-trainer/30min" /*, prefill, pageSettings, utm */,
              //   })
              // }
              class="btn btn-3"
            >
              {cta_call}
            </a>
          </div>
        </Intro>
      </MainContainer>

      {/* <PostList posts={posts} /> */}
      {/* <StyledLink
        css={`
          display: block;
          margin-top: var(--size-800);
          margin-bottom: var(--size-800);
          margin-left: auto;
          margin-right: auto;
          width: fit-content;
        `}
        to="/blog"
      >
        View All posts
      </StyledLink> */}
    </Layout>
  );
};

export default HomePage;
const MainContainer = styled.div`
  // backdrop-filter: blur(16px) saturate(180%);
  // -webkit-backdrop-filter: blur(16px) saturate(180%);
  // background-color: rgba(17, 25, 40, 0.75);
  // border-radius: 12px;
  // border: 1px solid rgba(255, 255, 255, 0.125);
  // padding: 0.5em 0.7em;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  @media screen and (max-width: 800px) {
    flex-direction: column;
    align-items: center;
  }
  max-width: min(1300px, 100%);
  margin-right: auto;
  margin-left: auto;
`;

const Intro = styled.div`
  display: flex;
  flex: 3;
  flex-direction: column;
  max-width: min(60ch, 100%);
  // align-items: center;
  margin-right: auto;
  margin-left: auto;
  margin-top: var(--size-800);
  margin-bottom: var(--size-900);
  // text-align: center;
  // min-width: 500px;
  padding: 0 3em;

  & p {
    // text-transform: capitalize;
    font-size: var(--size-400);
  }
  & h1 {
    padding-bottom: 0.5em;
  }
  & .button-container {
    display: flex;
    flex-wrap: wrap;
    & a.store-link {
      // flex: 0 1;
      height: 40px;
      width: 150px!important;
      & span {
        height: 40px;
        max-width: 150px!important;
        margin: unset;
        // height: 40px;
        // width: auto;
        box-shadow: none;
        & img {
          margin: unset;
          object-fit: contain;
        }
        }
      }
    }
    & .btn {
      text-decoration: none;
      text-align: center;
      padding: 15px 30px;
      margin: 10px;
      font-size: 18px;
      font-weight: 900;
      color: #373856;
      background-color: white;
      border: 2px solid white;
      cursor: pointer;
      width: max-content;
      transition: 0.25s ease;
      &-1:hover {
        border-style: dashed;
      }
      &-2:hover {
        border-radius: 25px;
      }
      &-3:hover {
        color: white;
        background-color: transparent;
      }
      &-4:hover {
        box-shadow: 0 6px 18px 0 rgba(#000, 0.1);
        color: $primary;
        border-color: transparent;
        background-color: transparent;
      }
      &-5:hover {
        box-shadow: 0 6px 18px 0 rgba(#000, 0.1);
        transform: translateY(-6px);
      }
      &--transparent {
        color: $primary;
        background-color: transparent;
      }
    }
    
  }
  @media screen and (max-width: 1200px) {
    padding: 0 1.5em;
    & .btn {
      font-size: 15px;
      padding: 8px 16px;
    }

  }

  @media screen and (max-width: 800px) {
    margin-top: 1em;
    margin-bottom: 1em;
    min-width: unset;
    & h1 {
      font-size: var(--size-650);
    }
    & h4 {
      font-weight: 500;
    }
    & .button-container{
      justify-content: center;
      & .btn{
        flex: 0 1;
        min-width: min(300px,90%);
        font-size: 18px;
        padding: 15px 30px;
      }
      
    }
  }
`;

const FlexCont = styled.div`
  flex: 4;
  padding: 1.5em;
  margin: auto;
  @media screen and (max-width: 1000px) {
    padding: 1.5em 0;
    max-width: 85%;
    min-width: 450px;
  }
  @media screen and (max-width: 800px) {
    max-width: 450px;
    min-width: 85%;
    padding: 0.5em 0;
  }
`;
const VideoContainer = styled.div`
  position: relative;
  // padding: 1.5rem;
  width: 100%;
  height: 0;
  padding-top: 66%;
  // border-style: solid;
  // border-width: 100px 30px 130px 30px;
  // border-image: url(/media/tablet.svg);
  // border-image-slice: 109 48 110 48;
  // // overflow: hidden;
  // @media screen and (max-width: 500px) {
  //   border-width: 50px 20px 60px 20px;
  //   border-image-slice: 109 48 110 48;
  // }
`;

const TabletShape = styled.div`
  // position: absolute;
  // // border-style: solid;
  // top: 0;
  // left: 0;
  // bottom: 0;
  // right: 0;
  // content: url(/media/tablet.svg);
`;

const VideoElement = styled.iframe`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid black;
`;

export const pageQuery = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { fields: { contentType: { eq: "posts" } } }
      sort: { order: DESC, fields: frontmatter___date }
      limit: 9
    ) {
      nodes {
        fields {
          slug
        }
        excerpt
        timeToRead
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          description
          title
          tags
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        url
        cta_mail
        cta_call
      }
    }
  }
`;
